export default function reducer(state={
    flights:[],
    fetching: false,
    fetched: false,
    error: null
}, action){
    switch (action.type) {
        case "FETCH_FLIGHTS":{
            return {...state, fetching: true}
        }
        case "FETCH_FLIGHTS_FULFILLED":{
            return {
                ...state,
                fetching: false,
                fetched: true,
                flights: action.payload
            }
        }
        case "FETCH_FLIGHTS_REJECTED":{
            return {
                ...state,
                fetching: false,
                error: action.payload
            }
        }
        default: {

        }
    }
    return state;
}


