import axios from "axios";

export function fetchFlights(){
    return function(dispatch){
        axios.get('http://lapa.demo2.ru/data.json')
            .then((response) =>{
                dispatch({type:'FETCH_FLIGHTS_FULFILLED', payload: response.data.flights});
            })
            .catch((err) => {
                dispatch({type:'FETCH_FLIGHTS_REJECTED', payload: err})
            })
    }
}



