import React, {Component} from 'react'
import './FlightCard.css'

class FlightCard extends Component {
    render() {
        const item = this.props.item;
        return (
            <section className="flight-card">

                <div className="flight-card__item">
                    <div className="flight-card__title">{item.direction.from}</div>
                    <time dateTime={item.departure}>{this.formatDate(item.departure)}</time>
                </div>

                <div className="flight-card__separator"></div>

                <div className="flight-card__item flight-card__item--right">
                    <div className="flight-card__title">{item.direction.to}</div>
                    <time dateTime={item.arrival}>{this.formatDate(item.arrival)}</time>
                </div>

                <div className="flight-card__carrier">{item.carrier}</div>
            </section>
        );
    }

    formatDate = (newDate) => {
        const day = new Date(newDate);
        const dd = day.getDate(),
              mm = day.getMonth() + 1,
              yyyy = day.getFullYear(),
              hh = day.getHours(),
              min = day.getMinutes();

        const format = function(unit) {
            return unit < 10 ? '0' + unit : unit;
        };

        return format(dd) + '.' + format(mm) + '.' + yyyy + ' ' + format(hh) + ':' + format(min);
    }

}

export default FlightCard


