import React, { PureComponent } from 'react';
import { connect } from 'react-redux'
import {fetchFlights} from "../actions/FlightsActions"
import './App.css';
import FlightsList from '../components/FlightsList';

class App extends PureComponent {
    state = {
        selectedCarrier: 'all'
    };

    componentWillMount(){
        this.props.dispatch(fetchFlights());
    };

    render() {
        const initialFlights = this.props.flights;

        const uniqueCarriers = new Set();
        const uniqueCarriersOptions = [];
        initialFlights.forEach(function (item) {
            uniqueCarriers.add(item.carrier);
        });
        uniqueCarriers.forEach(function (item, index) {
            uniqueCarriersOptions.push(
                <option value={item} key={item}>{item}</option>
            );
        });

        const flights = initialFlights.filter((item) => {
                return item.carrier === this.state.selectedCarrier || this.state.selectedCarrier === 'all'
            }
        );


        return (
            <div className="App">

                <div className="container">

                    <header className="header">
                        <h1>Flight Schedule</h1>
                    </header>

                    <div className="flights-select">
                        <label htmlFor="carrier" className="flights-select__label">Choose the carrier:</label>
                        <select className="flights-select__select" id="carrier"
                                value={this.state.selectedCarrier}
                                onChange = {this.changeCarrier}>
                            <option value="all" key="0">All carriers</option>
                            {uniqueCarriersOptions}
                        </select>
                    </div>

                    <FlightsList flights={flights} />

                </div>

            </div>
        );
    }

    changeCarrier = (e) => {
        this.setState({selectedCarrier: e.target.value});
    }
}

function mapStateToProps (store) {
    return {
        flights: store.FlightsReducer.flights
    }
}

export default connect(mapStateToProps)(App)


